@include tablet {
    .bottom-section {
        display: grid;
        grid-template-columns: 2fr 1fr;
        column-gap: 2rem;
    }
}

.blog-entry {
    margin-bottom: 2rem;

    @include tablet {
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }
    &:last-of-type {
        margin-bottom: 0;
    }
}

.information-meta {
    span {
        color: $amarillo;
    }
}

.text-entry{
    a{
        color: $negro;
        margin-top: 2rem;
        display: block;
        @include tablet{
            margin-top: 0;
        }
    }
    h4 {
        margin: 0;
        line-height: 1.4;

        &:after {
            content: '';
            display: block;
            width: 15rem;
            height: .5rem;
            background-color: $verde;
            margin-top: 1rem;
        }
    }
    
}