.no-webp .image-contact{
    background-image: url('../img/encuentra.jpg');
}
.webp .image-contact{
    background-image: url('../img/encuentra.webp');
}

.image-contact {
    background-position: center center;
    background-size: cover;
    padding: $separacion *2 0;
    display: flex;
    flex-direction: column;
    align-items: center;

    h2{
        font-size: 4rem;
        font-weight: $black;
        color: $blanco;

    }
    p{
        font-size: 1.8rem;
        color: $blanco;
    }
}